@font-face {
  font-family: 'avenirnextltpro';
  src: url("fonts/avenirnextltpro/AvenirNextLTPro-Bold.eot");
  src: url("fonts/avenirnextltpro/AvenirNextLTPro-Bold.woff") format("woff"), url("fonts/avenirnextltpro/AvenirNextLTPro-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'avenirnextltpro';
  src: url("fonts/avenirnextltpro/AvenirNextLTPro-Regular.eot");
  src: url("fonts/avenirnextltpro/AvenirNextLTPro-Regular.woff") format("woff"), url("fonts/avenirnextltpro/AvenirNextLTPro-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

/*Variables*/
.title-container {
  position: relative;
  z-index: -5;
}

.title {
  position: relative;
  padding: 1em;
  background: radial-gradient(#308fcf 33%, #066CAF);
  box-shadow: inset 0px -2px 12px -4px white;
}

.title:before,
.title:after {
  content: "";
  position: absolute;
  z-index: -2;
}

.curva:before {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
  top: 50%;
  bottom: 0;
  left: 10px;
  right: 10px;
  border-radius: 185px / 35px;
}

.title .curva .curva-h {
  box-sizing: border-box !important;
}

/* Universal Selectors */
/* Element Selectors */
body {
  height: 100%;
  background-color: #066CAF;
  background: radial-gradient(#308fcf 33%, #066CAF);
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "AvenirNextLTPro", verdana, arial, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

/* Class, Attribute, and Pseudo Selectors */
.title, .titles {
  width: 100%;
  text-align: center;
  color: white;
}

.title {
  width: 98%;
}

.title h2 {
  text-shadow: 0 -1px 0 black;
  marign-top: 2%;
}

.title .sub-title {
  font-size: 25px;
}

.sub-title-2 {
  font-size: 23px;
}

.doctors {
  margin-top: 35px;
}

.doctors .padding-doctor, .programs .padding-program {
  width: 49%;
  display: inline-block;
  box-sizing: border-box;
  min-width: 320px;
  min-height: 50px !important;
  overflow: hidden;
  height: 105px;
}

.padding-doctor .doctor-card, .program-card {
  border-radius: 90px 90px 90px 90px;
  background-color: #368ECA;
  width: 75%;
  min-width: 320px;
  max-width: 460px;
  margin: 0 auto;
  height: 103px;
  position: relative;
  box-shadow: inset 1px 1px 5px 0px white, inset -2px -2px 5px #5EB0E6, 1px 1px 5px #2F2D2D;
}

.programs {
  margin-top: 50px;
  height: 210px;
}

.padding-program {
  height: 210px !important;
}

.program-card {
  border-radius: 20px;
  background-color: #1A75B3;
  height: 200px;
  width: 65%;
}

.border-image {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  top: 10%;
  left: 4%;
  border-style: solid;
  border-color: white;
}

.doctor-card .doctor-name {
  top: 5%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px black;
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;
}

.doctor-card .doctor-code, .program-name {
  top: 40%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px black;
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;
}

.program-name {
  font-size: 32px;
  left: 10%;
  top: 25%;
  font-weight: bold;
  width: 8em;
}

.back-btn {
  display: inline-block;
  top: 15px;
  left: 10px;
  width: 84px;
  line-height: 48px;
  position: absolute;
  text-decoration: none;
  background: url("./img/back-button-sprite.png") 0 0;
}

.back-btn:hover {
  background-position: 0px -48px;
}

