/* !important ID Selectors */
/* media queries */
@media (max-width: 719px) {
  .doctor-card .doctor-name {
    width: 8em;
  }
  .doctor-card .doctor-code {
    width: 8em;
  }
}

@media (max-width: 719px) {
  .educational-titles h1 {
    width: 80%;
    margin: 0 auto;
  }
  .sub-title-2 {
    width: 80%;
    margin: 0 auto;
  }
  .padding-program {
    display: block !important;
    margin: 0 auto;
  }
}

@media (max-width: 679px) {
  .padding-doctor {
    display: block !important;
    margin: 5% auto;
  }
}

@media (max-width: 819px) {
  .titles h1 {
    margin-top: 70px;
  }
}
